import axios from "axios";

const axiosApi = axios.create({
  baseURL: `/api`,
  timeout: 1000,
  headers: { "Content-Type": "application/json" },
});

export default {
  hello() {
    return axiosApi.get(`/hello`);
  },
  getSecured(user, password) {
    return axiosApi.get(`/auth/admin`, {
      auth: {
        username: user,
        password: password,
      },
    });
  },
};
